import React from 'react'
import makeStyles from "@mui/styles/makeStyles";
import Paper from '@mui/material/Paper';
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";

const useStyles = makeStyles(() => ({
  img: {
      width: "100%",
      // height: "auto",
      height: "500px",
      objectFit: "cover",
      backgroundColor: "#000"
  },
  footer: {
    position: "absolute",
    bottom: 0,
    left: 0,
    right: 0,
    height: "60%",
    padding: "32px",
    borderRadius: "32px 32px 0 0 !important"
  }
}));
const BoxSum = ({title, subtitle}) => {
  return (
    <Box border={1} padding={1} borderRadius={3}>
      <Grid container spacing={1}>
        <Grid item xs={12}>
          <Typography variant="body1">{title}</Typography>
        </Grid>
        <Grid item xs={12}>
          <Typography variant="subtitle1">{subtitle}</Typography>
        </Grid>
      </Grid>
    </Box>
  );
 
}
const DetailDoctor = () => {
  const classes = useStyles();
    return (
      <>
        <div className={classes.img}></div>
        <Paper className={classes.footer}>
          <Grid container spacing={2}>
            <Grid item container spacing={2} xs={12}>
              <Grid item xs={6}>
                <Grid item xs={12}>
                  <Typography variant="subtitle1">Drs Qahtan Said</Typography>
                </Grid>
                <Grid item xs={12}>
                  <Typography variant="body1">Paru</Typography>
                </Grid>
              </Grid>
              <Grid item xs={6}>
                <Grid item xs={12}>
                  <Typography align="right" variant="body1">
                    Mulai Dari
                  </Typography>
                </Grid>
                <Grid item xs={12}>
                  <Typography align="right" variant="subtitle1">
                    RP.15.000
                  </Typography>
                </Grid>
              </Grid>
            </Grid>
            <Grid item container spacing={2}>
              <Grid item xs>
                <BoxSum title={"pasien"} subtitle={"1000+"} />
              </Grid>
              <Grid item xs>
                <BoxSum title={"pasien"} subtitle={"1000+"} />
              </Grid>
              <Grid item xs>
                <BoxSum title={"pasien"} subtitle={"1000+"} />
              </Grid>
            </Grid>
            <Grid item container spacing={2}>
              <Grid item xs={12}>
                <Button variant="contained">Buat Jadwal</Button>
              </Grid>
              <Grid item xs={12}>
                <Button variant="outlined">Lihat Jadwal Praktek</Button>
              </Grid>
            </Grid>
          </Grid>
        </Paper>
      </>
    );
}

export default DetailDoctor;